.dashboard-holder {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    height: calc(100vh - 80px);
    background-color: #f6f7fc;
    padding: 15px 0px;
    .db-box {
        position: relative;
        height: 100%;
        border-radius: 15px;
        box-shadow: 1px 9px 50px -17px rgba(0,0,0,0.54);
        -webkit-box-shadow: 1px 9px 50px -17px rgba(0,0,0,0.54);
        -moz-box-shadow: 1px 9px 50px -17px rgba(0,0,0,0.54);
    }
}